import React from 'react';
import styled from "styled-components";
import Fade from 'react-reveal/Fade';
import picture from "../images/pictures/leaf.png";
import photo from "../images/photos/photo1.png";

const ContentContainer = styled.div`
    padding-top: 15%;
    padding-bottom: 15%;
`;

const TitleDiv = styled.div`
    padding-bottom: 10%;
    font-family: 'NanumSquareacB';
    font-size: 100%;
`;

const BodyDiv = styled.div`
    padding: 2%;
`;

const StyledSpan = styled.span`
    margin-left:2%;
    font-family: 'NanumSquareAceb';
`;

const Picture = styled.img`
    width: 80%;
    padding-bottom: 5%;
    padding-top: 5%;
`;

const Photo = styled.img`
    width: 100%;
    padding-top: 10%;
`;


export default function ContentsContainer() {
    return (<div>
            <ContentContainer>
                <Fade bottom>
                    <Picture src={picture}/>
                </Fade>

                <Fade bottom cascade>
                    <TitleDiv>초대합니다.</TitleDiv>
                    <BodyDiv>9월의 좋은 날</BodyDiv>
                    <BodyDiv>결혼합니다.</BodyDiv>
                    <BodyDiv>그 자리에 함께하여</BodyDiv>
                    <BodyDiv>축복해주시면 감사하겠습니다.</BodyDiv>
                </Fade>
                <Fade bottom>
                    <Picture src={picture}/>
                </Fade>
                <Fade bottom>
                    <Photo src={photo}/>
                </Fade>
                <Fade bottom cascade>
                    <div className="nameSection">
                        <div className="nameDiv"><span>김용석 · 김난주</span><span className="smallFont">의 장남</span><StyledSpan>세중</StyledSpan></div>
                        <div className="nameDiv"><span>최성대 · 백순조</span><span className="smallFont">의 장녀</span><StyledSpan>유정</StyledSpan></div>
                    </div>
                </Fade>
            </ContentContainer>
        </div>
    )
        ;
}